<template>
  <div class="gateway condition2">
    <div class="search-box">
      <ul>
        <li>
          <p class="input-title">IMEI</p>
          <input type="text" v-model="queryFilter.imei" placeholder="请输入IMEI"/>
        </li>
        <li>
          <p class="input-title">设备状态</p>
          <div class="dropmenu">
            <input type="text" readonly="readonly" placeholder="请选择"/>
            <div class="dropdown">
              <ul>
                <li @click="queryFilter.status=-1">全部状态</li>
                <li @click="queryFilter.status=1">在线</li>
                <li @click="queryFilter.status=0">离线</li>
              </ul>
            </div>
          </div>
        </li>
        <li class="search-btn">
          <el-button class="opacity" type="primary" @click="initData">查询</el-button>
        </li>
      </ul>
    </div>

    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloaddata="initData"
      >
        <div slot="col-is_working" slot-scope="scope"><p>{{scope.row.is_working === 0 ? '离线' : '在线'}}</p></div>
      </iot-table>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import { findAllStations } from '@/api/dataCenter'
import iotTable from '@/components/iotTable'

export default {
  name: 'Gateway',
  components: {
    iotTable
  },
  mounted() {
    // 下拉
    $('.dropmenu').click(function() {
      $(this).children('.dropdown').slideToggle();
    });
    $('.dropdown li').click(function() {
      var value = $(this).text();
      $(this).closest('.dropmenu').find('input').val(value);
    });
    this.initData()
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        imei: '',
        status: '-1'
      },
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'imei', label: 'imei' },
        { prop: 'version', label: '版本号' },
        { prop: 'connection_num', label: '连接次数' },
        { prop: 'is_working', label: '当前状态', type: 'slot' },
        { prop: 'address', label: '街道' }
      ]
    }
  },
  methods: {
    async initData() {
      this.loading = true
      const param = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        imei: this.queryFilter.imei ? this.queryFilter.imei : undefined,
        is_working: this.queryFilter.status >= 0 ? this.queryFilter.status : undefined
      }
      const { data } = await findAllStations(param)
      // console.log(data)
      this.tableData = data.items
      this.loading = false
    }
  }
}
</script>

