<template>
  <div class="popup popup1 task-popup" id="startOta" v-show="showStatus">
    <p class="popup-title">
      基站OTA
    </p>
    <div class="popup-con">
      <ul>
        <li>
          <span>基站IMEI标识</span>
          <p>{{station.imei}}</p>
        </li>
        <li>
          <span>基站固件版本</span>
          <p>{{station.version}}</p>
        </li>
        <li>
          <span>基站安装位置</span>
          <p>{{station.address}}</p>
        </li>
        <li>
        <!-- <li :class="{'warning': $v.station.password.$invalid}"> -->
          <span>升级密钥(4位数字)</span>
          <p><input type="text" v-model="station.password"/></p>
        </li>
        <li>
          <button class="btn btn-middle cancel" @click="cancel">取消</button>
          <button class="btn btn-middle yes" @click="submit">确定</button>
        </li>
      </ul>
    </div>
    <!--关闭按钮-->
    <span class="layui-layer-setwin">
      <a class="close-popup layui-layer-close layui-layer-close1" href="javascript:;" @click="cancel">
        <img src="../../../assets/img/close.png" title="关闭"/>
      </a>
    </span>
  </div>
</template>
<script>
import { required } from 'vuelidate/lib/validators'
import { withParams } from 'vuelidate/lib/validators/common'
import { stationStartOta } from '@/api/dataCenter'
import msgUtil from '@/utils/msgUtil'

export default {
  data() {
    return {
      showStatus: false,
      station: {
        imei: '',
        address: 0,
        version: '',
        password: ''
      }
    }
  },
  // validations: {
  //   station: {
  //     password: {
  //       required,
  //       format: withParams({ type: 'format' }, val => {
  //         return val && /^[0-9]{4}$/.test(val)
  //       })
  //     }
  //   }
  // },
  methods: {
    show(info) {
      this.station = {
        imei: info.imei,
        address: info.address,
        version: info.version
      }
      this.showStatus = true
    },
    submit() {
      // if (!this.$v.station.$invalid) {
      stationStartOta(this.station.imei, { 'password': this.station.password }).then(success => {
        this.showStatus = false
        msgUtil.success('向在线基站下发OTA指令成功')
      }, error => {
        msgUtil.error('向基站下发OTA指令失败', msgUtil.findErrorMsg(error))
      })
      // }
    },
    cancel() {
      this.showStatus = false
    }
  }
}
</script>
