<template>
  <div class="upgrade">
    <div class="data">
      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloadData="initData"
      >
        <div slot="col-middle" slot-scope="scope"><button class="btn btn-middle" @click="upgrade_station(scope.index)">启动OTA</button></div>
      </iot-table>
    </div>
    <start-ota ref="startOta"></start-ota>
  </div>
</template>

<script>
import { stationsByVer } from '@/api/dataCenter'
import startOta from './startOta.vue'
import iotTable from '@/components/iotTable'

export default {
  name: 'Upgrade',
  components: {
    startOta,
    iotTable
  },
  mounted() {
    this.initData()
  },
  data() {
    return {
      loading: true,
      tableData: [],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableHeader: [
        { prop: 'address', label: '地址' },
        { prop: 'imei', label: 'imei' },
        { prop: 'version', label: '版本号' },
        { prop: 'middle', label: '升级进度', type: 'slot' }
      ]
    }
  },
  methods: {
    async initData() {
      this.loading = true
      const { data } = await stationsByVer('0.2')
      this.tableData = data.items
      this.loading = false
    },
    upgrade_station(idx) {
      this.$refs.startOta.show(this.tableData[idx])
    }
  }
}
</script>

